<script>
import axios from 'axios';
import feather from "feather-icons";
import DataTable from '@/utils/dataTable';
import "datatables.net-bs5";
import "datatables.net-bs5/css/dataTables.bootstrap5.min.css";
import moment from 'moment';


export default {
    mixins: [DataTable],
    props: {
        id: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            dataTable: null,
            mostrarModal1: false,
            mostrarModal2: false,
            mostrarModal3: false,

            escritos: [],
            ejes: [],
            EJE: {
                name: "",
                exp_id: "",
                doc_tipo: "EJE",
                descripcion: ""
            },
            escrito: {
                exp_id: "",
                doc_tipo: "ESCRITO",
                descripcion: ""
            },
            formattedDate: "",
        };
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    async created() {
        this.sector = this.$store.getters.get__sector;
        this.cargarDetallesExpediente();
        this.dataTable = null;
    },
    methods: {
        async cargarDetallesExpediente() {
            try {
                this.$showLoadingAlert();
                const response = await this.$getData(`proceeding/${this.id}`, this);
                this.EJE.exp_id = response.expediente.exp_id;
                this.escrito.exp_id = response.expediente.exp_id;

                this.escritos = response.escritos;
                await this.$waitForDataTableDraw(this, 'myDataTable');
                this.$closeLoadingAlert();
            } catch (error) {
                this.$handleApiError(error, 'cargar detalles del expediente ', this.id);
            }
        },

        // toggleModal(modalId) {
        //     const modalElement = document.getElementById(modalId);
        //     const modal = new Modal(modalElement, { backdrop: 'static' });
        //     modal.show();
        // },
        // cerrarModal(modalId) {
        //     const modalElement = document.getElementById(modalId);
        //     const modal = Modal.getInstance(modalElement) || new Modal(modalElement);
        //     modal.hide();
        // },
        async navigateToPJ() {
            const nuevaPestana = window.open('https://cej.pj.gob.pe/cej/forms/busquedaform.html', '_blank');
        },
        cerrarModal() {
            this.modal = '';
        },

        formatDate() {
            this.formattedDate = moment(this.expediente.exp_fecha_inicio).format('DD/MM/YYYY');
        },

        formatFecha(fechaISO) {
            const fecha = new Date(fechaISO);
            const opcionesFormato = {
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                hour: 'numeric',
                minute: 'numeric',
                second: 'numeric'
            };
            return fecha.toLocaleDateString('es-ES', opcionesFormato);
        },

        async traerArchivo(name, doc) {
            try {
                this.$showLoadingAlert('Descargando...', 'Obteniendo archivo, espere por favor...');

                const response = await axios.get(this.$store.getters.get__url + '/traer/archivo', {
                    responseType: 'blob',
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.get__token,
                    },
                    params: {
                        nombre: name,
                    },
                });

                const blob = new Blob([response.data], {
                    type: response.headers['content-type'],
                });
                this.$closeLoadingAlert();
                const cleanFileName = doc.replace(/[^\w\s.]/gi, '-');
                const url = window.URL.createObjectURL(blob);
                const link = document.createElement('a');
                link.href = url;
                link.download = cleanFileName;
                link.click();

            } catch (error) {
                console.error('Error al abrir el archivo', error);
            }
        },

        goBack() {
            this.$router.go(-1);
        },

        async nuevoEje() {
            this.EJE.exp_id = this.id;
            this.EJE.doc_tipo = "EJE";
            if (this.archivoSeleccionado) {
                try {
                    this.$showLoadingAlert();
                    const formData = new FormData();
                    formData.append('file', this.selectedFile);

                    await axios.post(
                        `${this.$store.getters.get__url}/cargar/archivo`,
                        formData,
                        {
                            headers: {
                                'Content-Type': 'multipart/form-data',
                                Authorization: 'Bearer ' + this.$store.getters.get__token,
                            },
                            params: this.EJE,
                        }
                    );
                    console.info('Carga correcta del archivo eje.');
                    this.selectedFile = null;
                    this.showNEW = false;
                    this.cerrarModal;
                    this.$closeLoadingAlert();
                    this.cargarDetallesExpediente();
                } catch (error) {
                    console.error(error);
                }
            } else {
                console.log('Falta cargar un archivo.');
            }
        },

        async actualizarEje() {
            if (this.archivoSeleccionado) {
                try {
                    this.$showLoadingAlert();
                    const formData = new FormData();
                    formData.append('file', this.selectedFile);

                    await axios.post(
                        `${this.$store.getters.get__url}/cargar/archivo/eje`,
                        formData,
                        {
                            headers: {
                                'Content-Type': 'multipart/form-data',
                                Authorization: 'Bearer ' + this.$store.getters.get__token,
                            },
                            params: this.EJE,
                        }
                    );
                    console.info('Carga correcta del archivo eje.');
                    this.selectedFile = null;
                    this.showModal = false;
                    this.cerrarModal;
                    this.$closeLoadingAlert();
                    this.cargarDetallesExpediente();
                } catch (error) {
                    console.error(error);
                }
            } else {
                console.log('Falta cargar un archivo.');
            }
        },

        async nuevoEscrito() {
            if (this.archivoSeleccionado) {
                console.info('Carga correcta del archivo escrito.');
                this.errores.file_escrito = false;
                const formData = new FormData();
                formData.append('file', this.selectedFile);

                try {
                    // Eliminar el código de SweetAlert para la carga
                    await axios.post(`${this.$store.getters.get__url}/cargar/archivo`, formData, {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                            Authorization: `Bearer ${this.$store.getters.get__token}`,
                        },
                        params: this.escrito,
                    });

                    // Eliminar el código de SweetAlert para la confirmación
                    // this.$refs.fileInput.value = null;
                    this.selectedFile = null;
                    this.$router.push({ path: '/expediente/busqueda' });

                } catch (error) {
                    // Eliminar el código de SweetAlert para el manejo de errores
                    console.error(error);
                }
            } else {
                console.log('Falta cargar un archivo.');
                this.errores.file_escrito = true;
            }
        },

        onFileChange(event) {
            this.selectedFile = event.target.files[0];
            const fileInput = event.target;
            this.archivoSeleccionado = fileInput.files.length > 0;
        },
    },
};
</script>

