                    <div class="card-body pt-0 px-0">
                        <div class="table-responsive">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th class="pl-4">Parte</th>
                                        <th>Tipo de Persona</th>
                                        <th>Apellido Paterno / Razon Social</th>
                                        <th>Apellido Materno</th>
                                        <th class="pr-4">Nombres</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="demandante in demandantes">
                                        <td class="pl-4">Demandante</td>
                                        <td>{{ demandante.tipo_persona === 'NATURAL' ? 'Natural' : 'Jurídica' }}
                                        </td>
                                        <td>{{ demandante.tipo_persona === 'NATURAL' ? demandante.apellido_paterno :
                                            demandante.razon_social }}</td>
                                        <td>{{ demandante.tipo_persona === 'NATURAL' ? demandante.apellido_materno :
                                            '--' }}</td>
                                        <td class="pr-4">{{ demandante.tipo_persona === 'NATURAL' ?
                                            demandante.nombres :
                                            '--' }}</td>
                                    </tr>
                                    <tr v-for="demandado in demandados">
                                        <td class="pl-4">Demandado</td>
                                        <td>{{ demandado.tipo_persona === 'NATURAL' ? 'Natural' : 'Jurídica' }}</td>
                                        <td>{{ demandado.tipo_persona === 'NATURAL' ? demandado.apellido_paterno :
                                            demandado.razon_social }}</td>
                                        <td>{{ demandado.tipo_persona === 'NATURAL' ? demandado.apellido_materno :
                                            '--' }}</td>
                                        <td class="pr-4">{{ demandado.tipo_persona === 'NATURAL' ?
                                            demandado.nombres :
                                            '--' }}</td>
                                    </tr>
                                </tbody>
                            </table>

                        </div>
