</template>

<script>
import { mapActions } from 'vuex';
import axios from 'axios';
import feather from "feather-icons";
import "datatables.net-bs5";
import "datatables.net-bs5/css/dataTables.bootstrap5.min.css";
import moment from 'moment';

export default {
    props: {
        id: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            dataTable: null,
            expediente: null,
            demandantes: [],
            demandados: [],
            formattedDate: "",
        };
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    async created() {
        this.sector = this.$store.getters.get__sector;
        this.cargarDetallesExpediente();
        this.dataTable = null;
    },
    methods: {

        async cargarDetallesExpediente() {
            try {
                this.$showLoadingAlert();
                const response = await this.$getData(`proceeding/${this.id}`, this);
                this.expediente = response.expediente;
                this.formatDate();
                this.getProcesales(response.procesales);
                this.initDataTable();
                this.$closeLoadingAlert();
            } catch (error) {
                this.$handleApiError(error, 'cargar detalles del expediente ', this.id);
            }
        },
        formatDate() {
            this.formattedDate = moment(this.expediente.exp_fecha_inicio).format('DD/MM/YYYY');
        },

        getProcesales(procesales) {
            if (procesales[0].tipo_procesal === 'DEMANDANTE') {
                this.demandantes = procesales;
                this.tipo_procesal = 'DEMANDANTE';
                const newDemandante = {
                    'tipo_procesal': 'Demandado',
                    'tipo_persona': 'Juridica',
                    'razon_social': 'UNPRG'
                };
                this.demandados.push(newDemandante)
            } else {
                this.demandados = procesales;
                const newDamandado = {
                    'tipo_procesal': 'Demandado',
                    'tipo_persona': 'Juridica',
                    'razon_social': 'UNPRG'
                };
                this.demandantes.push(newDamandado);
            }
        },

        formatFecha(fechaISO) {
            const fecha = new Date(fechaISO);
            const opcionesFormato = {
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                hour: 'numeric',
                minute: 'numeric',
                second: 'numeric'
            };
            return fecha.toLocaleDateString('es-ES', opcionesFormato);
        },

        goBack() {
            this.$router.go(-1);
        },

        initDataTable() {
            this.$nextTick(() => {
                if (this.dataTable) {
                    this.dataTable.destroy();
                }

                this.dataTable = $("#table-escritos").DataTable({
                    language: {
                        url: "//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json",
                    },
                });

                feather.replace();
            });
        },

    },

    beforeDestroy() {
        if (this.dataTable) {
            this.dataTable.destroy();
        }
    },
};
</script>

