<template>
    <div class="pc-container pb-4 ">
        <div class="pcoded-content">
            <div class="row mb-4">
                <h5 class="">
                    <button class="btn btn-sm mr-2 btn-secondary" @click="goBack">
                        <i data-feather="arrow-left"></i>
                    </button><span class="text-primary">Expediente</span> | <span class="text-secondary"> {{
                        numeroExpediente }}</span>
                </h5>
            </div>
            <div class="row mb-4">
                <div class="col-md-2">
                    <button type="button" class="btn btn-outline-primary w-100"
                        :class="{ 'btn-primary text-white': isVista === '1' }" @click="selectVista('1')">
                        Detalles
                    </button>
                </div>
                <div class="col-md-2">
                    <button type="button" class="btn btn-outline-primary w-100"
                        :class="{ 'btn-primary text-white': isVista === '2' }" @click="selectVista('2')">
                        Archivos
                    </button>
                </div>
                <div class="col-md-2">
                    <button type="button" class="btn btn-outline-primary w-100"
                        :class="{ 'btn-primary text-white': isVista === '3' }" @click="selectVista('3')">
                        Tareas
                    </button>
                </div>
            </div>
            <Detalle :id="expedienteId" v-if="isVista==='1'"/>
            <Archivos :id="expedienteId" v-else-if="isVista==='2'"/>
        </div>
    </div>
</template>

<script>
import Detalle from '@/components/expediente_detalle/Detalle.vue';
import Archivos from '@/components/expediente_detalle/Archivos.vue';


import feather from "feather-icons";
export default {
    props: ['id'],
    components: {
        Detalle, Archivos
    },
    data() {
        return {
            isVista: '1',
            numeroExpediente: '',
            expedienteId: this.id
        };
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    async created() {
        this.cargarDetallesExpediente();
    },
    methods: {
        selectVista(vista) {
            this.isVista = vista;
        },
        async cargarDetallesExpediente() {
            try {
                this.$showLoadingAlert();
                const response = await this.$getData(`proceeding/${this.id}`, this);
                this.numeroExpediente = response.expediente.exp_numero;
                this.$closeLoadingAlert();
            } catch (error) {
                this.$handleApiError(error, 'cargar detalles del expediente ', this.id);
            }
        },

        goBack() {
            this.$router.push({ path: '/expediente/busqueda' });
        },
    },
};
</script>